// @flow
'use strict'

const classListSupport = !(typeof document.createElement('span').classList !== 'undefined');
const classNameSupport = !(typeof document.createElement('span').className !== 'undefined');

// $FlowIgnore
export const rnothtmlwhite = /[^\x20\t\r\n\f]+/g;

export function stripAndCollapse (value: string): string {
  const tokens = value.match(rnothtmlwhite) || [];
  return tokens.join(' ');
}

/**
 *
 * @param {string|Array} value
 * @return {string[]}
 */
export function classesToArray (value: string | Array<string>): string[] {
  if (Array.isArray(value)) {
    return value;
  }
  if (typeof value === 'string') {
    return value.match(rnothtmlwhite) || [];
  }
  return [];
}

/**
 *
 * @param {HTMLElement} el
 * @param {string} value
 */
export function toggleClass (el: HTMLElement, value: string): boolean {
  const classNames = classesToArray(value);

  if (classNames.length && el.nodeType === Node.ELEMENT_NODE) {
    classNames.forEach(className => {
      if (classListSupport) {
        el.classList.toggle(className);
      } else {
        if (hasClass(el, className)) {
          removeClass(el, className);
        } else {
          addClass(el, className);
        }
      }
    });

    return true;
  }

  return false;
}

/**
 *
 * @param {HTMLElement} el
 * @return {string}
 */
export function getClass (el: HTMLElement): string {

  if (el.nodeType !== Node.ELEMENT_NODE) {
    return '';
  }

  if (classNameSupport) {
    return el.className;
  }

  return el.getAttribute('class') || '';
}

/**
 *
 */
export function hasClass (el: HTMLElement, value: string): boolean {

  if (el.nodeType !== Node.ELEMENT_NODE) {
    return false;
  }

  if (classListSupport) {
    return el.classList.contains(value);
  }
  const classNames = classesToArray(getClass(el));
  return classNames.indexOf(value) > -1;
}

/**
 *
 * @param {HTMLElement | Node} el
 * @param {string} value
 */
export function addClass (el: HTMLElement, value: string): void {

  let classNames = classesToArray(value), curClass, cur, final;

  if (classNames.length && el.nodeType === Node.ELEMENT_NODE) {
    if (classListSupport) {
      el.classList.add(...classNames);
    } else {
      curClass = getClass(el);
      cur = ' ' + stripAndCollapse(curClass) + ' ';
      classNames.forEach(className => {
        if (cur.indexOf(' ' + className + ' ') < 0) {
          cur += className + ' '
        }
      });

      final = stripAndCollapse(cur);

      if (curClass !== final) {
        if (classNameSupport) {
          el.className = final;
        } else {
          el.setAttribute('class', final);
        }
      }
    }
  }
}

/**
 *
 * @param {HTMLElement | Node} el
 * @param {string} [value]
 */
export function removeClass (el: HTMLElement, value: string): void {

  let classNames = classesToArray(value), curClass, cur, final,
    isElement = (el.nodeType === Node.ELEMENT_NODE);

  if (arguments.length < 2 && isElement) {
    return el.removeAttribute('class');
  }

  if (classNames.length && isElement) {
    if (classListSupport) {
      el.classList.remove(...classNames);
    } else {
      curClass = getClass(el);
      cur = ' ' + stripAndCollapse(curClass) + ' ';
      classNames.forEach(className => {
        while (cur.indexOf(' ' + className + ' ') > -1) {
          cur = cur.replace(' ' + className + ' ', ' ');
        }
      });

      final = stripAndCollapse(cur);
      if (curClass !== final) {
        if (classNameSupport) {
          el.className = final;
        } else {
          el.setAttribute('class', final);
        }
      }
    }
  }
}
